.container {
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc; /* Border color */
  border-radius: 8px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Box shadow */
  background-color: #e6f7ff; /* Light blue background color */
}

.header {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  color: #333;
}

input {
  width: 94%;
  padding: 10px;
  margin-bottom: 10px;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.List-heading {
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 10px;
  color: #333;
}

.listData {
  font-size: 16px;
}

.btn-position {
  margin-left: 10px;
}

button#removeAllBtn {
  background-color: #d9534f;
}

button#removeAllBtn:hover {
  background-color: #c9302c;
}
